nav {
	background-color: rgba(0, 0, 0, 1);
	padding: 2rem;
	display: flex;
	justify-content: center;
	border-bottom: 1px solid $white;

	button {
		padding: 1rem;
		font-size: 1.6rem;
		color: $white;
		border-color: $white;
		border-width: 1px;
		background-color: transparent;
		transition: all ease-in-out 0.3s;

		&:not(:first-of-type) {
			margin-left: clamp(1rem, 0.1765rem + 2.3529vw, 3rem);
		}

		&.selectedTab {
			background-color: $white;
			color: $black;
			border-color: $black;
		}
	}
}
