$layout-large: 120em;
$layout-medium: 76.8em;
$layout-small: 48em;
$layout-mobile: 30em;

@mixin layout-large {
	@media only screen and (max-width: $layout-large) {
		@content;
	}
}
@mixin layout-medium {
	@media only screen and (max-width: $layout-medium) {
		@content;
	}
}
@mixin layout-small {
	@media only screen and (max-width: $layout-small) {
		@content;
	}
}

@mixin layout-mobile {
	@media only screen and (max-width: $layout-mobile) {
		@content;
	}
}
