.tab-container {
	text-align: center;
	margin: 0 auto;
}
.module {
	&-overview {
		img {
			height: clamp(25rem, 21.789rem + 9.1743vw, 35rem);
			max-width: 100%;
		}

		h3 {
			margin-top: 0;
			margin-bottom: 1rem;
		}

		p {
			margin-bottom: 5rem;
		}

		.competencies {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			align-items: center;
			margin-bottom: 5rem;

			img {
				width: 3rem;
				height: 3rem;
				&:not(:first-of-type) {
					margin-left: 1rem;
				}
			}
			span {
				width: 3rem;
				height: 3rem;
				font-size: 3rem;
				rotate: -90deg;
				margin-left: 1rem;
			}
		}
	}

	&-projects {
		&_container {
			section {
				margin-bottom: clamp(2rem, 0rem + 4.1667vw, 5rem);
				.site-preview {
					height: clamp(15rem, 1.6667rem + 27.7778vw, 35rem);
					&.bf {
						background: url("../assets/bread-financial.png") top
							center no-repeat;
						background-size: 100%;
					}

					&.coupang {
						background: url("../assets/coupang.png") top center
							no-repeat;
						background-size: 100%;
					}

					&.delmonte {
						background: url("../assets/delmonte.png") top center
							no-repeat;
						background-size: 100%;
					}

					&.fbf {
						background: url("../assets/fbf.png") top center
							no-repeat;
						background-size: 100%;
					}

					&.jb-hunt {
						background: url("../assets/jb-hunt.png") top center
							no-repeat;
						background-size: 100%;
					}

					&.qs {
						background: url("../assets/qs.png") top center no-repeat;
						background-size: 100%;
					}

					@include layout-mobile {
						height: clamp(20rem, -6.9231rem + 76.9231vw, 30rem);
						&.bf {
							background: url("../assets/bread-financial_mobile.png")
								top center no-repeat;
							background-size: contain;
						}

						&.coupang {
							background: url("../assets/coupang_mobile.png") top
								center no-repeat;
							background-size: contain;
						}

						&.delmonte {
							background: url("../assets/delmonte_mobile.png") top
								center no-repeat;
							background-size: contain;
						}

						&.fbf {
							background: url("../assets/fbf_mobile.png") top
								center no-repeat;
							background-size: contain;
						}

						&.jb-hunt {
							background: url("../assets/jb-hunt_mobile.png") top
								center no-repeat;
							background-size: contain;
						}

						&.qs {
							background: url("../assets/qs_mobile.png") top
								center no-repeat;
							background-size: contain;
						}
					}
				}
			}
		}
	}

	&-contact {
		margin-top: 2rem;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin: 0 auto;

		h3 {
			margin-bottom: 1rem;
			margin: 0 auto 1rem auto;
		}
		a {
			margin-bottom: 1rem;
			display: flex;
			align-items: center;

			&::before {
				display: block;
				height: 2rem;
				width: 2rem;
				margin-right: 1rem;
			}

			&:first-of-type {
				&::before {
					content: "";
					background: url("../assets/icons/email.png") center center
						no-repeat;
					background-size: 100%;
				}
			}

			&:nth-of-type(2) {
				&::before {
					content: "";
					background: url("../assets/icons/linkedin.png") center
						center no-repeat;
					background-size: 100%;
				}
			}

			&:nth-of-type(3) {
				&::before {
					content: "";
					background: url("../assets/icons/pdf.png") center center
						no-repeat;
					background-size: 100%;
				}
			}
		}
	}
}
