body {
	background-color: $bg;
}

.wrapper {
	max-width: 120rem;
	width: 90%;
	margin: 0 auto;
}

a {
	text-decoration: none;
}

a:hover,
button:hover {
	cursor: pointer;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding-bottom: 5%;
}
