body {
	color: $white;
	font-family: "Work Sans", sans-serif;
}

h1,
h2 {
	margin: 2rem 0;
}

h1,
h2 {
	font-size: clamp(4.4rem, 2.4444rem + 2.5463vw, 5.5rem);
}

h3 {
	font-size: clamp(1.8rem, 1.4rem + 0.8333vw, 2.4rem);
}

p,
a {
	font-size: clamp(1.5rem, 1.3rem + 0.4167vw, 1.8rem);
}

a {
	color: $white;

	&:hover {
		text-decoration: underline;
	}
}

@include layout-medium {
	h1,
	h2 {
		font-size: clamp(3.4rem, 2.5627rem + 2.3923vw, 4.4rem);
	}
}
